.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.loader-wrapper {
  width: 400px;
  height: 120px;
  position: absolute;
  top: 65%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.circle {
  width: 40px;
  height: 40px;
  transform-origin: 50%;
  background-color: #ff001b;
  border-radius: 50%;
  animation: circle .5s infinite alternate;
  position: absolute;
  left: 15%;
}

@keyframes circle {
  0% {
    height: 10px;
    border-radius: 100px 100px 50px 50px;
    top: 60px;
    transform: scaleX(1.7);
  }

  40% {
    height: 40px;
    border-radius: 50%;
    transform: scaleX(1);
  }

  100% {
    top: 0%;
  }
}

.circle:nth-child(2) {
  animation-delay: .2s;
  left: 45%;
}

.circle:nth-child(3) {
  animation-delay: .3s;
  left: auto;
  right: 15%;
}

.shadow {
  width: 40px;
  height: 8px;
  transform-origin: 50%;
  z-index: -1;
  filter: blur(1px);
  background-color: rgba(0, 0, 0, .5);
  border-radius: 50%;
  animation: shadow .5s infinite alternate;
  position: absolute;
  top: 62px;
  left: 15%;
}

@keyframes shadow {
  0% {
    transform: scaleX(1.5);
  }

  40% {
    opacity: .7;
    transform: scaleX(1);
  }

  100% {
    opacity: .4;
    transform: scaleX(.2);
  }
}

.shadow:nth-child(4) {
  animation-delay: .2s;
  left: 45%;
}

.shadow:nth-child(5) {
  animation-delay: .3s;
  left: auto;
  right: 15%;
}

.loader-wrapper span {
  letter-spacing: 12px;
  color: #ff001b;
  margin-top: 20px;
  font-family: Roboto, sans-serif;
  font-size: 45px;
  position: absolute;
  top: 60px;
  left: 50%;
  transform: translate(-50%, 50%);
}

body.show-modal .backdrop {
